@import "../../style.scss";


.navbar {
  @include themify($themes){

  
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 20px;
  height: 50px;
  border-bottom: 1px solid themed("border");
  position: sticky;
  top: 0;
  background-color: themed("bg");
  color:themed("textcolor");
  z-index: 999;


  .left {
    display: flex;
    align-items: center;
    gap: 30px;

    span {
      font-size: 20px;
      font-weight: bold;
      color:themed("logo")
    }

    .search {
      display: flex;
      align-items: center;
      gap: 30px;
      border: 1px solid themed("border");
      border-radius: 5px;
      padding: 5px;
      

      input {
        border: none;
        width: 500px;
        height: 30px;
        background-color: transparent;
        color: themed("textcolor");

        @include mobile{
          display: none;
        }
        @include tablet{
          width: 200px;
        }
      }
    }
  }

  .right {
    display: flex;
    align-items: center;
    gap: 20px;

    @include mobile{
      display: none;
    }

    .user {
      display: flex;
      align-items: center;
      gap: 10px;
      font-weight: 500;

      @include tablet{
        display: none;
      }

      img {
        width: 30px;
        height: 30px;
        border-radius: 50%;
        object-fit: cover;
      }

      span {
        display: flex;
        align-items: center;
      }
    }
  }
}
}